.collections__title {
  padding-top: 35px;
  padding-left: 16px;
  margin-bottom: 0;
  font-family: 'Rajdhani', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
}

.collections__cartoon {
  border-radius: 4px;
  width: 100%;
  margin-top: 7px;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  z-index: -1;
  &-kingsman {
    position: absolute;
    bottom: 12px;
    left: 32px;
    font-family: 'Rajdhani', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 24px;
    color: #fff;
    &-description {
      position: absolute;
      bottom: 2px;
      left: 32px;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 16px;
      color: #fff;
      opacity: 0.5;
    }
  }
  &-denim {
    position: absolute;
    bottom: 12px;
    left: 32px;
    font-family: 'Rajdhani', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 24px;
    color: #fff;
    &-description {
      position: absolute;
      bottom: 2px;
      left: 32px;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 16px;
      color: #fff;
      opacity: 0.5;
    }
  }
}

.collections__position {
  padding: 0 16px;
  position: relative;
  &:last-child {
    margin-top: 4px;
  }
}

@media(min-width: 720px) {
  .collections__cartoon {
    &-kingsman {
      font-size: 40px;
      line-height: 40px;
      bottom: 6px;
      &-description {
        font-size: 18px;
        line-height: 18px;
      }
    }
    &-denim {
      bottom: 6px;
      font-size: 40px;
      line-height: 40px;
      &-description {
        font-size: 18px;
        line-height: 18px;
      }
    }
  }
  .collections__position {
    &:last-child {
      margin-top: 16px;
    }
  }
}

@media(min-width: 1360px) {
  .collections__position {
    width: 45.3%;
    padding-left: 12px;
    padding-right: 0;
    &:last-child {
      margin-top: 0;
      margin-left: 12px;
    }
  }
  .collections__wrapper {
    display: flex;
  }
  .collections__title {
    font-size: 16px;
    line-height: 24px;
    padding-left: 12px;
  }
  .collections__cartoon {
    height: 160px;
    &-denim {
      bottom: 13px;
      left: 28px;
      font-size: 24px;
      line-height: 24px;
      &-description {
        left: 28px;
        bottom: 2px;
        font-size: 14px;
        line-height: 16px;
      }
    }
    &-kingsman {
      bottom: 14px;
      left: 28px;
      font-size: 24px;
      line-height: 24px;
      &-description {
        left: 29px;
        bottom: 2px;
        font-size: 14px;
        line-height: 16px;
      }
    }
  }
}