.page {
  min-width: 100%;
  height: 100%;
}

.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}

@media (min-width: 1360px) {
  .page__body {
    grid-template-columns: 1fr 1fr;
  }
}
@font-face {
  font-weight: normal;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Rajdhani", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-style: normal;
  font-weight: normal;
  color: #8a94a6;
  background-color: white;
  background-position: top center;
  background-repeat: no-repeat;
}

img {
  max-width: 100%;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.slider {
  width: 100%;
  position: relative;
  margin: 0 auto;
}

.slider-item {
  animation: fade 2.5s forwards;
}
.slider-item-title {
  margin-top: 396px;
  margin-bottom: 0;
  padding-left: 16px;
  font-family: "Rajdhani", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 48px;
  color: #fff;
}
.slider-item-page {
  padding-top: 12px;
  margin-left: 186px;
  font-family: "Rajdhani", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 32px;
  color: #fff;
}
.slider-item-page--vision {
  opacity: 0.5;
}
.slider-item-page--number {
  margin-left: -10px;
  margin-right: 15px;
}
.slider-item-description {
  margin-top: 0;
  padding-left: 16px;
  font-family: "Rajdhani", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}
.slider-item--hidden {
  display: none;
}
.slider-item--light::before {
  content: "";
  position: absolute;
  background-image: url("../img/light.webp");
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  width: 100%;
  height: 600px;
  top: 0;
  z-index: -1;
}
.slider-item--sandy {
  display: none;
}
.slider-item--sandy::before {
  content: "";
  position: absolute;
  background-image: url("../img/lightmobile.webp");
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  width: 100%;
  height: 620px;
  top: 0;
  z-index: -1;
}
.slider-item--lime {
  display: none;
}
.slider-item--lime::before {
  content: "";
  position: absolute;
  background-image: url("../img/niga.webp");
  background-size: cover;
  background-repeat: no-repeat;
  object-fit: cover;
  width: 100%;
  height: 600px;
  top: 0;
  z-index: -1;
}

.slider-item-cartoon {
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
}

.slider-next,
.slider-pre {
  cursor: pointer;
  position: absolute;
  top: 98%;
  width: auto;
  margin-top: -17px;
  padding: 12px;
  color: #fff;
  outline: 0;
  border: 0;
  background-color: transparent;
  background-image: url("../img/arrow.webp");
  background-repeat: no-repeat;
  background-position: 40%;
}

.slider-next {
  left: 70px;
}

.slider-pre {
  left: 18px;
  font-weight: 700;
  font-size: 16px;
  transform: rotate(180deg);
}

@keyframes fade {
  0% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}
.visible-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.slider__logo {
  width: 25%;
  position: relative;
  top: 24px;
  left: 16px;
  z-index: 1;
}

.slider-pre:hover {
  top: 97%;
  background-image: url("../img/arrowhover.webp");
  transform: rotate(360deg);
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
}

.slider-next:hover {
  top: 97%;
  background-image: url("../img/arrowhover.webp");
  background-repeat: no-repeat;
  transform: rotate(180deg);
  width: 40px;
  height: 40px;
}

.slider-next:active {
  top: 97%;
  background-image: url("../img/arrowactive.webp");
  background-repeat: no-repeat;
  transform: rotate(180deg);
  width: 40px;
  height: 40px;
}

.slider-pre:active {
  top: 97%;
  background-image: url("../img/arrowactive.webp");
  transform: rotate(360deg);
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
}

@media (min-width: 720px) {
  .slider-item-title {
    margin-top: 380px;
    font-size: 64px;
    line-height: 64px;
  }
  .slider-item-description {
    margin-bottom: 0;
    font-size: 24px;
    line-height: 32px;
  }
  .slider-item--light::before {
    background-image: url("../img/tablet.webp");
  }
  .slider-item--sandy::before {
    background-image: url("../img/tebletnig.webp");
  }
  .slider-item--lime::before {
    background-image: url("../img/tablelime.webp");
  }
  .slider-item-page {
    padding-top: 4px;
  }
}
@media (min-width: 1360px) {
  .slider {
    position: fixed;
    width: 44.1%;
  }
  .slider-pre {
    margin-top: -33px;
    left: 54px;
  }
  .slider-next {
    margin-top: -33px;
    left: 106px;
  }

  .slider__logo {
    top: 32px;
    left: 52px;
  }

  .slider-item-title {
    margin-top: 510px;
    padding-left: 52px;
    font-size: 72px;
    line-height: 72px;
  }
  .slider-item-description {
    padding-left: 52px;
    font-size: 28px;
  }
  .slider-item--light::before {
    background-image: url("../img/desktop.webp");
    height: 768px;
  }
  .slider-item--sandy::before {
    background-image: url("../img/desktopnig.webp");
    height: 768px;
  }
  .slider-item--lime::before {
    background-image: url("../img/desktoplime.webp");
    height: 768px;
  }
  .slider-item-page {
    margin-top: 16px;
    padding-top: 16px;
    padding-left: 36px;
  }
}
.order__blue {
  position: relative;
}
.order__blue::before {
  content: "";
  position: absolute;
  background-color: #192b3f;
  border-radius: 50%;
  top: -5px;
  left: 20px;
  width: 40px;
  height: 40px;
}

.order__gray {
  position: relative;
}
.order__gray::before {
  content: "";
  position: absolute;
  background-color: #8a94a6;
  border-radius: 50%;
  width: 40px;
  top: -4px;
  height: 40px;
  left: 72px;
}

.order__azure {
  position: relative;
}
.order__azure::before {
  content: "";
  position: absolute;
  background-color: #3d5876;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  left: 124px;
  top: -4px;
}

.order__point:checked + label::before {
  border: 3px solid #fff;
  box-shadow: 0 0 0 1px #192b3f;
  border-radius: 50%;
}

.order__point:hover + label::before {
  border: 3px solid #fff;
  box-shadow: 0 0 0 1px #8a94a6;
  border-radius: 50%;
}

.order__title {
  padding-top: 3px;
  padding-left: 16px;
