.subscribe__title {
  padding-top: 28px;
  padding-left: 16px;
  margin-bottom: 0;
  font-family: 'Rajdhani', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  color: #192b3f;
}

.subscribe__description {
  padding-left: 16px;
  padding-right: 50px;
  margin-top: 6px;
  font-family: 'Rajdhani', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  color: #8a94a6;
  opacity: 0.8;
}

.subscribe__form {
  display: flex;
  flex-direction: column;
  &-link {
    margin: 13px 16px;
    padding-top: 1.2px;
    padding-left: 16px;
    font-family: 'Rajdhani', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 40px;
    border: none;
    background-color: rgba(225, 230, 237, 0.2);
    &-position {
      margin-top: 5px;
    }
    &::placeholder {
      color: #8a94a6;
    }
  }
}

.subscribe__gender {
  padding-left: 16px;
  margin-bottom: 30px;
  &-title {
    margin-top: 0;
    margin-bottom: 4px;
    font-family: 'Rajdhani', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 32px;
    color: #8a94a6;
    opacity: 0.8;
  }
  &-view {
    padding-left: 28px;
    font-family: 'Rajdhani', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: #192b3f;
    opacity: 0.8;
    &--circle {
      position: relative;
      &::before {
        content: '';
        position: absolute;
        width: 20px;
        height: 20px;
        bottom: 1px;
        left: 0;
        border: 1px solid #e1e6ed;
        border-radius: 16px;
      }
    }
    &--ring {
      position: relative;
      &::before {
        content: '';
        position: absolute;
        width: 20px;
        height: 20px;
        bottom: 1px;
        left: 20px;
        border: 1px solid #e1e6ed;
        border-radius: 16px;
      }
    }
    &:last-child {
      padding-left: 48px;
    }
  }
}

.subscribe__gender-point:checked+.subscribe__gender-view--circle {
  &::after {
    content: '';
    position: absolute;
    width: 12px;
    height: 12px;
    top: 4px;
    left: 4px;
    background: #192b3f;
    border-radius: 16px;
  }
}

.subscribe__gender-point:checked+.subscribe__gender-view--ring {
  &::after {
    content: '';
    position: absolute;
    width: 12px;
    height: 12px;
    top: 4px;
    left: 24px;
    background: #192b3f;
    border-radius: 16px;
  }
}

.subscribe__check-done {
  position: relative;
  padding-left: 44px;
  &::before {
    content: '';
    position: absolute;
    top: 1px;
    left: 16px;
    width: 20px;
    height: 20px;
    background: #fff;
    border: 1px solid #e1e6ed;
    box-sizing: border-box;
    border-radius: 1px;
  }
  font-family: 'Rajdhani',
  sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  opacity: 0.8;
  color: #192b3f;
}

.subscribe__check:checked+.subscribe__check-done {
  &::after {
    content: '';
    position: absolute;
    width: 10px;
    height: 9px;
    top: 6px;
    left: 21px;
    background-image: url('../img/check.svg');
    background-repeat: no-repeat;
  }
  &::before {
    border: none;
    background-color: #192b3f;
  }
}

.subscribe__check:hover+.subscribe__check-done::before {
  border: 1px solid #192b3f;
  background-image: url('../img/checkhover.svg');
  background-repeat: no-repeat;
  background-position: center;
}

.subscribe__gussi {
  margin: 16px 16px;
  height: 56px;
  padding-left: 3px;
  outline: none;
  border: none;
  cursor: pointer;
  font-family: 'Rajdhani', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  color: #fff;
  background: #192b3f;
  &:hover {
    background-color: #2e4055;
  }
  &:active {
    background-color: #3b536d;
  }
}

@media(min-width: 720px) {
  .subscribe__title {
    padding-top: 16px;
    padding-left: 17px;
    font-size: 32px;
    line-height: 40px;
  }
  .subscribe__description {
    padding-top: 0;
    margin-top: 0;
    margin-bottom: 14px;
    font-size: 20px;
    line-height: 32px;
  }
  .subscribe__gender {
    display: flex;
    margin-bottom: 34px;
    &-view {
      padding-top: 4px;
      padding-left: 53px;
      &:last-child {
        padding-left: 53px;
      }
      &--ring::before {
        left: 25px;
        bottom: 11px;
      }
      &--circle::before {
        bottom: 11px;
        left: 26px;
      }
    }
  }
  .subscribe__gender-point:checked+.subscribe__gender-view--circle {
    &::after {
      top: 9px;
      left: 30px;
    }
  }
  .subscribe__gender-point:checked+.subscribe__gender-view--ring {
    &::after {
      top: 9px;
      left: 29px;
    }
  }
  .subscribe__check-done {
    margin-bottom: 1px;
    &::before {
      left: 17px;
    }
  }
}

@media(min-width: 1360px) {
  .subscribe__title {
    padding-top: 32px;
    padding-left: 12px;
  }
  .subscribe__description {
    padding-left: 12px;
  }
  .subscribe__form-link {
    width: 67%;
    margin-left: 12px;
  }
  .subscribe__gender {
    padding-left: 12px;
  }
  .subscribe__check-done {
    padding-left: 40px;
    &::before {
      left: 12px;
    }
  }
  .subscribe__check:checked+.subscribe__check-done::after {
    left: 17px;
  }
  .subscribe__gussi {
    width: 90.8%;
    margin-left: 11px;
  }
}