.order__blue {
  position: relative;
  &::before {
    content: '';
    position: absolute;
    background-color: #192b3f;
    border-radius: 50%;
    top: -5px;
    left: 20px;
    width: 40px;
    height: 40px;
  }
}

.order__gray {
  position: relative;
  &::before {
    content: '';
    position: absolute;
    background-color: #8a94a6;
    border-radius: 50%;
    width: 40px;
    top: -4px;
    height: 40px;
    left: 72px;
  }
}

.order__azure {
  position: relative;
  &::before {
    content: '';
    position: absolute;
    background-color: #3d5876;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    left: 124px;
    top: -4px;
  }
}

.order__point:checked+label::before {
  border: 3px solid #fff;
  box-shadow: 0 0 0 1px #192b3f;
  border-radius: 50%;
}

.order__point:hover+label::before {
  border: 3px solid #fff;
  box-shadow: 0 0 0 1px #8a94a6;
  border-radius: 50%;
}

.order__title {
  padding-top: 3px;
  padding-left: 16px;
  font-family: 'Rajdhani', sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  &-litle {
    margin-top: 15px;
    padding-left: 16px;
    margin-bottom: 0;
    font-family: 'Rajdhani', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 32px;
    color: #192B3f;
  }
}

.order__description {
  padding-left: 16px;
  padding-right: 18px;
  margin-top: 14px;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    bottom: -18px;
    left: 16px;
    width: 90%;
    border-bottom: 1px solid #E1E6ED;
  }
}

.order__wrapper {
  display: flex;
  flex-direction: column;
  &-details {
    padding-top: 16px;
    position: relative;
    &-description {
      font-family: 'Rajdhani', sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      &-return {
        font-family: 'Rajdhani', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
      }
      &--litle {
        margin: 0;
      }
    }
    &::before {
      content: "";
      position: absolute;
      right: 0;
      top: 78px;
      width: 90%;
      left: 16px;
      border: 1px solid #e1e6ed;
    }
    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: -8px;
      width: 90%;
      left: 16px;
      border: 1px solid #e1e6ed;
    }
    &-position {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      flex-wrap: wrap;
      padding-left: 16px;
      padding-right: 19px;
    }
    &-show {
      margin: 0;
      padding: 0;
      position: relative;
      border: none;
      background: transparent;
      outline: none;
      cursor: pointer;
      font-size: 0;
      &::before {
        content: "";
        position: absolute;
        top: -27px;
        right: -5px;
        width: 20px;
        height: 21px;
        background: linear-gradient(#192b3f, #192b3f), linear-gradient(#192b3f, #192b3f);
        background-position: center;
        background-size: 50% 2px, 2px 50%;
        background-repeat: no-repeat;
      }
    }
    &-title {
      margin-bottom: 0;
      font-family: 'Rajdhani', sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 32px;
      color: #192b3f;
    }
  }
  &-product {
    margin-top: 9px;
    margin-bottom: 0;
    padding-left: 16px;
    font-family: 'Rajdhani', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    text-transform: uppercase;
    &-now {
      width: 90%;
      height: 56px;
      margin-top: 23px;
      margin-left: 16px;
      margin-right: 16px;
      padding-left: 2px;
      cursor: pointer;
      border: none;
      background: #192b3f;
      outline: none;
      font-family: 'Rajdhani', sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 32px;
      color: #fff;
      &:hover {
        background-color: #2e4055;
      }
      &:active {
        background-color: #3b536d;
      }
    }
    &-description {
      margin-top: 6px;
      padding-left: 16px;
      margin-bottom: 0;
      font-family: 'Rajdhani', sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      color: #192b3f;
      opacity: 0.8;
      &-info {
        margin-top: 10px;
        padding-left: 16px;
        font-family: 'Rajdhani', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #8a94a6;
      }
    }
  }
  &-quantity {
    display: flex;
    justify-content: space-between;
    padding-top: 17px;
    padding-left: 16px;
    text-transform: uppercase;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      right: 0;
      left: 14px;
      bottom: 20px;
      width: 90%;
      border: 1px solid #e1e6ed;
    }
    &-title {
      font-family: 'Rajdhani', sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 4px;
    }
    &-number {
      margin: 8px 31px;
      padding: 0;
      position: absolute;
      font-family: 'Rajdhani', sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 24px;
      color: #8a94a6;
    }
    &-plus {
      margin: 0;
      padding: 0;
      position: relative;
      border: none;
      background: transparent;
      outline: none;
      cursor: pointer;
      &::before {
        content: '';
        position: absolute;
        top: -4px;
        left: 56px;
        width: 20px;
        height: 21px;
        background: linear-gradient(#192b3f, #192b3f), linear-gradient(#192b3f, #192b3f);
        background-position: center;
        background-size: 50% 2px, 2px 50%;
        background-repeat: no-repeat;
      }
    }
    &-minus {
      margin: 0;
      padding: 0;
      position: relative;
      border: none;
      background: transparent;
      outline: none;
      cursor: pointer;
      &::before {
        content: '';
        position: absolute;
        top: 6px;
        left: 3px;
        width: 10px;
        height: 2px;
        background-color: #192b3f;
      }
    }
    &-cost {
      margin-right: 42px;
      &-description {
        margin-top: 0;
        font-family: 'Rajdhani', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 40px;
        line-height: 40px;
        color: #c9b8a8;
        &-sub {
          font-size: 30px;
          color: #8a94a6;
        }
      }
    }
  }
  &-title {
    font-family: 'Rajdhani', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    text-transform: uppercase;
    padding-left: 16px;
    &--size {
      padding-top: 42px;
      padding-left: 0;
      margin-bottom: 10px;
    }
  }
  &-position {
    padding-left: 16px;
    margin-top: 40px;
    &-send {
      margin-top: 35px;
      width: 92px;
      background-color: transparent;
      font-family: 'Rajdhani', sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      cursor: pointer;
      outline: none;
      border: 1px solid #C9b8a8;
      border-radius: 24px;
      color: #C9b8a8;
    }
    &-size {
      width: 100%;
    }
  }
}

.order__size {
  &-filter {
    font-family: 'Rajdhani', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    margin: 0 20px;
  }
  &--xs {
    margin: 0;
    margin-right: 14px;
  }
  &--m {
    margin-left: 12px;
  }
  &--l {
    margin-left: 10px;
  }
}

.order__size:checked+.order__size-filter {
  position: relative;
  font-size: 28px;
  color: #192b3f;
  &::before {
    content: '';
    position: absolute;
    border: 1px solid #000;
    border-radius: 50%;
    top: -6px;
    left: -17px;
    width: 48px;
    height: 48px;
  }
}

.order__size:hover+.order__size-filter {
  color: #8a94a6;
  &::before {
    content: '';
    position: absolute;
    border: 1px solid #8a94a6;
    border-radius: 50%;
    top: -6px;
    left: -17px;
    width: 48px;
    height: 48px;
  }
}

.order__size:disabled+.order__size-filter {
  color: #e1e6ed;
}

@media(min-width: 720px) {
  .order__title {
    &-litle {
      font-size: 48px;
      line-height: 56px;
    }
  }
  .order__description {
    font-size: 20px;
    line-height: 32px;
    padding-right: 70px;
    &::before {
      bottom: -6px;
      width: 96%;
    }
  }
  .order__wrapper {
    &-details {
      &::after {
        width: 95%;
      }
      &::before {
        width: 95%;
      }
    }
    &-product {
      margin-top: -3px;
      &-description {
        padding-right: 16px;
      }
      &-now {
        width: 95.5%;
      }
    }
    &-title {
      margin-top: 10px;
      &--size {
        margin: 0;
        padding: 0;
        padding-top: 10px;
        padding-bottom: 11px;
      }
    }
    &-form {
      display: flex;
    }
    &-position {
      display: flex;
      width: 100%;
      margin: 0;
      margin-left: 153px;
      &-size {
        width: 53%;
      }
      &-send {
        margin-top: 42px;
        margin-left: 2px;
        height: 28px;
      }
    }
    &-quantity {
      justify-content: left;
      &::before {
        width: 95%;
      }
      &-title {
        padding-top: 24px;
      }
      &-cost {
        margin-left: 158px;
      }
    }
  }
}

@media(min-width: 1360px) {
  .order__title {
    padding-top: 44px;
    padding-left: 12px;
    &-litle {
      padding-top: 8px;
      padding-left: 12px;
    }
  }
  .order__description {
    padding-left: 12px;
    padding-right: 265px;
    &::before {
      width: 90%;
      bottom: -10px;
    }
  }
  .order__gray::before {
    top: -7px;
    left: 53px;
    width: 26px;
    height: 26px;
  }
  .order__blue::before {
    top: -7px;
    left: 15px;
    width: 26px;
    height: 26px;
  }
  .order__azure::before {
    top: -7px;
    left: 91px;
    width: 26px;
    height: 26px;
  }
  .order__wrapper {
    &-product {
      padding-top: 8px;
      padding-left: 12px;
      &-description {
        padding-left: 12px;
        padding-right: 60px;
        &-info {
          padding-left: 12px;
        }
      }
      &-now {
        margin-top: 55px;
        width: 90.8%;
        margin-left: 12px;
      }
    }
    &-details {
      margin-left: -4px;
      padding-top: 8px;
      &::before {
        top: 70px;
        width: 90%;
      }
      &::after {
        width: 90%;
      }
      &-show::before {
        right: 29px;
      }
    }
    &-quantity {
      padding-top: 0;
      &::before {
        width: 90%;
      }
      &-cost {
        margin-left: 103px;
        &-description {
          margin-left: -4px;
        }
      }
      &-minus::before {
        left: -1px;
      }
      &-plus {
        &::after {
          content: '';
          position: absolute;
          top: -7px;
          left: 48px;
          width: 28px;
          height: 28px;
          border: 1px solid #e1e6ed;
          border-radius: 50%;
        }
        &::before {
          left: 52px;
        }
      }
      &-number {
        margin: 8px 28px;
      }
      &-title {
        padding-top: 20px;
        padding-left: 0;
        margin-left: -4px;
      }
    }
    &-title {
      margin-top: 8px;
      padding-left: 12px;
      &--size {
        padding-top: 0;
        padding-bottom: 7px;
      }
    }
    &-position {
      margin-left: 82px;
      &-size {
        width: 40%;
      }
      &-send {
        margin-top: 40px;
        margin-left: 0;
      }
    }
  }
  .order__size {
    &-filter {
      font-size: 16px;
      line-height: 24px;
    }
    &--xs {
      margin-left: 20px;
      margin-right: 0;
    }
    &--m {
      margin-left: 0;
    }
    &--l {
      margin-left: 5px;
    }
  }
  .order__size:checked+.order__size-filter {
    font-size: 20px;
  }
  .order__size:checked+.order__size-filter {
    &::before {
      top: 0;
      left: -12px;
      width: 32px;
      height: 32px;
    }
  }
}